<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <Markdown class="title is-1" :input="pageTitle" tag="h1" />
          <Markdown class="subtitle is-4" :input="pageSubTitle" tag="h2" />
        </div>
      </div>
    </section>

    <section class="container section-spacing">
      <div class="columns is-centered content">
        <div class="column is-6">
          <Markdown :input="pageContent" />
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="applyForTicket">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <h2 class="title is-3">Application Details</h2>
            <ApplicationQuestions
              class="section-spacing"
              v-model="applicationQuestions"
              :validations="$v.applicationQuestions"
            />

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              value="Apply For Ticket"
            />
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import Markdown from "@/components/Markdown";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";
import PersonInfo from "@/ticketing/components/PersonInfo";
import ApplicationQuestions from "@/ticketing/components/ApplicationQuestions";
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import {
  personQuestionsValidations,
  applicationQuestionsValidations
} from "@/mixins/questionsMixin";
import { mapGetters } from "vuex";

export default {
  name: "ApplicationsForm",
  components: {
    Markdown,
    PersonInfo,
    ApplicationQuestions
  },
  validations: {
    person: personQuestionsValidations,
    applicationQuestions: applicationQuestionsValidations
  },
  mixins: [markdownSubstitutionsMixin],
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person"
    }),
    applicationQuestions() {
      return this.person.data;
    },
    pageTitle() {
      return this.substituteContent(
        this.event.pageTicketApplyTitle,
        this.person,
        this.event
      );
    },
    pageSubTitle() {
      return this.substituteContent(
        this.event.pageTicketApplySubtitle,
        this.person,
        this.event
      );
    },
    pageContent() {
      return this.substituteContent(
        this.event.pageTicketApplyContentMarkdown,
        this.person,
        this.event
      );
    }
  },
  methods: {
    applyForTicket() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        ProfileService.createProfile({
          person: this.person,
          profile: this.applicationQuestions
        }).then(personRes => {
          TicketService.submitApplication(this.event.slug, {
            personId: personRes.data.person.id,
            ref: this.$route.query.ref ? this.$route.query.ref : null
          })
            .then(res => {
              if (res.data.error) {
                this.$snackbar.open({
                  message: res.data.error,
                  type: "is-danger",
                  position: "is-bottom-left"
                });
                NProgress.done();
              } else {
                this.$router.push({
                  name: "application-status",
                  params: {
                    eventSlug: this.event.slug,
                    id: res.data.applicationId
                  }
                });
              }
            })
            .catch(e => {
              console.log("ERROR", e);
            });
        });
      }
    }
  }
};
</script>
