<template>
  <applications-closed v-if="!event.applicationsActive" />
  <applications-form v-else />
</template>

<script>
import ApplicationsForm from "@/ticketing/views/ApplicationsForm";
import ApplicationsClosed from "@/ticketing/views/ApplicationsClosed";
import { mapGetters } from "vuex";

export default {
  name: "Applications",
  beforeMount() {
    if (event && !event.hasTickets) {
      this.$router.push({
        name: "404",
        params: { message: "This event doesn't have Tickets enabled." }
      });
    }
  },
  components: {
    ApplicationsClosed,
    ApplicationsForm
  },
  computed: {
    ...mapGetters({
      event: "events/event",
      person: "people/person"
    })
  }
};
</script>
